.Conatainer{
    width: auto;
    height: auto;
    padding: 1.5%;
    background-color: #212121;
    display: flex;
    justify-content: space-between;
}
a{
   text-decoration: none;
}
li{
    display: inline;
    color: #FFFF;
    font-size: large;
    margin-right: 4%;
}
.links{
    align-content: center;
    display: flex;
    width: 40%;
    margin: initial;
}
.logo{
    margin: initial;
    display: flex;
    justify-content: center;
}

.logo a{
    margin: initial;
}

a #figma, a #Twitter,a #Github,a #linkedIn{
    margin-right: 2vw;
    height: initial;
    width: 35%;
}

#figma:hover,#Twitter:hover,#Github:hover,#linkedIn:hover{
    cursor: pointer;
    transform: translateY(-3px);
}

li:hover{
    color: #0ACF83;
    cursor: pointer;
}


@media only screen and (max-width: 600px)  {
    li{
        font-size: xx-small;
    }
    #figma,#Twitter,#Github,#linkedIn{
        margin-right: 1vw;
    }
    .links{
        margin-top: -2%;
        width: 45%;
    }
}